@tailwind base;
@tailwind components;
@tailwind utilities;

/* Base de estilos para el cuerpo */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1a202c; /* Color de fondo oscuro por defecto */
}

/* Estilo para el fondo tipo WhatsApp */
.whatsapp-bg {
  background-color: #0f1c2b; /* Azul oscuro */
  /* ¡AQUÍ LA CORRECCIÓN DE LA RUTA DE LA IMAGEN! */
  background-image: url('../public/whatsapp-bg.png'); /* Ruta relativa desde src/ a public/ */
  background-size: cover; /* Cubre todo el elemento */
  background-position: center; /* Centra la imagen */
  background-repeat: repeat; /* Repite la imagen si es pequeña */
  background-blend-mode: multiply; /* Opcional: para mezclar con el color de fondo */
  opacity: 0.8; /* Ajusta la opacidad si la imagen es muy intensa */
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Altura completa de la pantalla para el contenedor principal de App */
#root {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Ocultar scrollbar pero permitir scroll */
.overflow-y-auto-hide-scrollbar {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}
.overflow-y-auto-hide-scrollbar::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Opera*/
}

/* Estilos para el texto de los mensajes (general y privado) */
/* Tus mensajes */
.message-bubble.mine {
  background-color: #005c4b; /* WhatsApp verde oscuro */
  color: #e2e8f0; /* Texto blanco suave */
  border-radius: 0.5rem 0.5rem 0 0.5rem; /* Esquina inferior izquierda recta */
  align-self: flex-end; /* Alineado a la derecha */
}

/* Mensajes de otros */
.message-bubble.other {
  background-color: #202c33; /* WhatsApp gris oscuro */
  color: #e2e8f0; /* Texto blanco suave */
  border-radius: 0.5rem 0.5rem 0.5rem 0; /* Esquina inferior derecha recta */
  align-self: flex-start; /* Alineado a la izquierda */
}

/* Estilos de burbujas de mensaje */
.message-bubble {
  padding: 0.5rem 0.75rem;
  margin-bottom: 0.5rem;
  max-width: 75%; /* Ajuste para móviles */
  word-wrap: break-word;
  box-shadow: 0 1px 0.5px rgba(0, 0, 0, 0.13); /* Sombra sutil */
}

.message-bubble .message-sender {
  font-weight: bold;
  font-size: 0.875rem; /* Tamaño de Nickname: 14px (small text) */
  margin-bottom: 0.25rem;
}

.message-bubble .message-text {
  font-size: 1rem; /* Tamaño de texto de mensaje: 16px (base text) */
  line-height: 1.25;
}

.message-bubble .message-timestamp {
  font-size: 0.65rem; /* ¡AQUÍ LA CORRECCIÓN: 10.4px, bien chico! */
  color: #a0aec0; /* gris suave */
  margin-top: 0.25rem;
  display: block;
  text-align: right;
}

/* Estilos de color para nicknames según el género */
.nickname-male {
  color: #63b3ed; /* Azul claro */
}

.nickname-female {
  color: #f687b3; /* Rosa claro */
}

.nickname-other {
  color: #a0aec0; /* Gris por defecto */
}


/* Notificación de mensajes no leídos (punto rojo) */
.notification-dot {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 10px;
    height: 10px;
    background-color: #ef4444; /* Rojo */
    border-radius: 50%;
    border: 1px solid #1a202c; /* Borde oscuro para contraste */
    z-index: 10; /* Asegurarse de que esté por encima de otros elementos */
}

/* Animación de pulso para la barra inferior */
.pulse-notification {
    animation: pulse-ring 1s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

@keyframes pulse-ring {
  0%, 100% {
    box-shadow: 0 0 0 0 rgba(239, 68, 68, 0.7);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(239, 68, 68, 0);
  }
}